import React from 'react';
import Projects from '../components/Projects';
import Experience from '../components/Experience';
import About from '../components/Sidebar/About';
import Links from '../components/Sidebar/Links';

import Typist from 'react-typist';

import './style.css';

const IndexPage = () => (
  <div className="index">
    <div className="left-side__container">
      <div className="left-side__top">
        <Typist
          className="left-side__typist"
          startDelay={200}
          cursor={{ show: false }}
        >
          <h1>Hello, 
            <h3>I'm Kevin!</h3>
          </h1>
        </Typist>
        <About />
      </div>
      <div className="left-side__bottom">
        <Links />
      </div>
    </div>
    <div className="right-side__container">
      <div className="right-side__about">
        <h3>Fullstack Engineer offering experience building products through the full software development life-cycle</h3>
      </div>
      <Projects />
      <Experience />
    </div>
  </div>
)

export default IndexPage;



