import React from 'react';

import './style.css';

class InformationUnit extends React.Component {
  render() {
    return (
      <div className="information-unit">
        {/* <a href={this.props.url} target="_blank">
          <img src={this.props.image} className="thumbnail" />
        </a> */}
        <div className="information-unit__title">
          <a href={this.props.url} target="_blank">
            {this.props.title}
          </a>
        </div>
        <div className="information-unit__dates">
          {this.props.dates}
        </div>
        <div className="information-unit__subtitle">
          {this.props.subtitle}
        </div>
        {this.props.tech ?
          <div className="information-unit__tech">
            Tech: {this.props.tech}
          </div>
        : null}
        {/* {this.props.url ?
          <div className="information-unit__url">
            {this.props.url}
          </div>
        : null} */}
      </div>
    )
  }
}

export default InformationUnit;


