import React from 'react';

import profilePic from '../../../../assets/profile_pic.png';

import './style.css';

const About = () => (
  <div className="about">
    <div className="about-image">
      <img src={profilePic} />
    </div>
    <div className="about-bio">
      Full Stack Software Engineer
    </div>
    {/* <div className="about-emoji">
    🏀🏌️🍷
    </div> */}
  </div>
)

export default About;


